@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container{margin:0 15%}p{margin:0}.bck-color{background-color:#141b4d;height:6vh}.header{display:flex;justify-content:space-between;align-items:center;text-align:center}.header_logo h1{margin:0;color:#fff}
.menutop{list-style:none;display:flex;grid-gap:1em;gap:1em;color:#fff;cursor:pointer}.selected{font-weight:700}.more{margin-right:5px}
.menutopsecund{font-size:.8em;list-style:none;display:flex;grid-gap:1em;gap:1em;color:#fff;cursor:pointer}.menutopsecund li{color:rgba(255,255,255,.7)}
.container{margin:0 15%}p{margin:0}.infouser{cursor:pointer;display:flex;grid-gap:1em;gap:1em;color:#fff;font-size:1.5em}.infouser_profile{display:flex;grid-gap:.2em;gap:.2em;align-items:center}.notification{position:relative}.notification .number{font-size:.6em;position:absolute;top:-4px;right:-7px;background-color:#23b9ef;content:"";border-radius:100%;padding:0 5px}.notification .number span{margin:0}
.container{margin:0 15%}p{margin:0}.slider{background-image:url("https://www.creativosonline.org/wp-content/uploads/2007/09/Modelos-3D-en-BLENDER.jpg");height:40vh;display:flex;flex-direction:column;justify-content:flex-end;background-repeat:no-repeat;background-size:cover}.slider .content{color:#fff}.slider .content p{margin:0;font-size:2em;font-weight:300}.slider .content h2{margin:0;font-size:3em;font-weight:700}.slider .content span{font-weight:700}.slider .button{cursor:pointer;margin-top:2em;background-color:transparent;border:solid 1px #fff;border-radius:3px;padding:.5em;color:#fff}.slider .button:hover{background-color:#fff;color:#141b4d}.slider .buttons{display:flex;flex-direction:row;justify-content:center;grid-gap:.5em;gap:.5em;margin-top:1em;margin-bottom:1em;cursor:pointer}.slider .buttons span{width:8px;height:8px;background-color:rgba(255,255,255,.5);border-radius:10px}.slider .buttons .selected{width:30px;background-color:#23b9ef}
.container{margin:0 15%}p{margin:0}.liveclass{display:grid;grid-gap:1em;gap:1em;grid-template-columns:65% 35%;padding-bottom:1em}.liveclass .livenow{text-align:center;color:#949494;margin:.5em}.liveclass .viewall{text-align:right;color:#949494;cursor:pointer}
.container{margin:0 15%}p{margin:0}.viewlive{display:flex;flex-direction:row;margin-bottom:1em;align-items:center;justify-content:center}.viewlive div{display:flex;grid-gap:.5em;gap:.5em}.viewlive_img{width:244px;height:138px;margin-right:.5em}.viewlive_img img{border-radius:3px;object-fit:cover;width:100%}.viewlive .infolive{border:solid 1px #949494;border-radius:3px;padding:.5em 1em;display:flex;flex-direction:column;justify-content:center;grid-gap:.2em;gap:.2em;width:-webkit-fill-available;max-height:138px}.viewlive .infolive .place{display:flex;align-items:center;color:#949494}.viewlive .infolive .place .live{background-color:red;padding:0 .5em;border-radius:3px;color:#fff;margin-right:.5em}.viewlive .infolive .title{display:flex;align-items:center;justify-content:space-between}.viewlive .infolive .title h3{margin:0}.viewlive .infolive .title button{margin-left:.5em;min-width:-webkit-max-content;min-width:-moz-max-content;min-width:max-content;padding:.5em;background-color:red;border:none;border-radius:3px;color:#fff;display:flex;grid-gap:.3em;gap:.3em;cursor:pointer}.viewlive .infolive .on{display:flex;grid-gap:.5em;gap:.5em;color:#949494}.viewlive .infolive .on p{color:#000;font-weight:700}.viewlive .infolive .viewers{color:#949494}
.container{margin:0 15%}p{margin:0}.comingsoon{display:flex;flex-direction:row;margin-bottom:1em;align-items:center;border:solid 1px #949494;border-radius:3px}.comingsoon_date{display:flex;flex-direction:column;margin-right:.5em;padding:0 1em;border-right:solid 1px #949494}.comingsoon_date p{white-space:nowrap;text-align:center;color:#949494}.comingsoon_date p:first-child{font-size:1.5em;margin-bottom:.2em}.comingsoon_info{padding:.5em;display:flex;flex-direction:column;justify-content:center;grid-gap:.2em;gap:.2em}.comingsoon_info .place{display:flex;align-items:center;color:#949494}.comingsoon_info .place .live{background-color:red;padding:0 .5em;border-radius:3px;color:#fff;margin-right:.5em}.comingsoon_info h3{margin:0}.comingsoon_info .timeleft{display:flex}.comingsoon_info .timeleft span{margin-right:.5em;color:#949494}
.container{margin:0 15%}p{margin:0}.bck-color{background-color:#141b4d;width:100%;height:auto}.footer{display:flex;flex-direction:column;padding:1em 0;grid-gap:1em;gap:1em}.footer_top{display:flex;justify-content:space-between;align-items:center;color:#fff}.footer_top div{font-size:1.2em;display:flex;color:#949494}.footer_top div span{font-weight:800;margin-left:.3em;color:#fff}.footer_top h1{margin:0}.footer_top .button{background-color:#23b9ef;border:none;padding:1em 1.5em;color:#fff;border-radius:10px;cursor:pointer}.footer_list{list-style:none;display:flex;grid-gap:1em;gap:1em;justify-content:center;border-bottom:solid 1px #949494;padding:1em}.footer_list li{color:#949494;cursor:pointer;padding:.5em;border-radius:3px}.footer_list li:hover{background-color:#fff;color:#000}
.container{margin:0 15%}p{margin:0}.footer_botton{display:grid;grid-template-columns:70% 30%;color:#949494}.footer_botton_contact{display:flex;flex-direction:column}.footer_botton_contact .social-media{display:flex;justify-content:center;grid-gap:1em;gap:1em;padding:2em;font-size:1.3em}.footer_botton_contact .social-media svg{cursor:pointer}.footer_botton_contact .social-media svg:hover{color:#fff}.footer_botton_contact .menu{display:flex;justify-content:center;grid-gap:1em;gap:1em;color:#fff;margin:1em 0;font-size:.9em}.footer_botton_contact .menu li{white-space:nowrap;list-style-type:none;cursor:pointer}.footer_botton_contact .copyright{margin-top:.5em}.footer_botton_contact .copyright p{margin-bottom:.5em}.footer_botton_contact .copyright div{display:flex;grid-gap:1em;gap:1em}.footer_botton_contact .copyright div li{list-style:none;cursor:pointer}.footer_botton_contact .copyright div li:hover{color:#fff}.footer_botton .newsletter{display:flex;flex-direction:column;justify-content:center;padding:.5em}.footer_botton .newsletter h2{margin:0;color:#fff;font-weight:300}.footer_botton .newsletter p{margin:.5em 0}.footer_botton .newsletter button{background-color:#23b9ef;border:none;color:#fff;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;padding:1em 2em;border-radius:3px;margin:1em auto 0 auto;cursor:pointer}.footer_botton .newsletter button:hover{background-color:#fff;color:#141b4d}
.oneView {
  height: 100vh;
}

